import React from "react";
import Link from "gatsby-link";

const ListLink = props =>
  <li>
    <Link to={props.to}>
      {props.children}
    </Link>
  </li>


export default () => (
  <div>

    <h2>Articles</h2>

    <h3>Managing data science projects</h3>

    <p>Here's a framework I use to manage data science projects. I divide them into four stages: discovery, research, production and ongoing operation. I’ll give examples of the outcomes and timelines of each stage on a small three-month project. <Link to="/articles/managing-data-science-projects">Read more...</Link></p>

    <h3>Time, cost and outcomes in data science projects</h3>

    <p>Most data science projects require researching approaches and techniques, collecting and processing data, and testing different model architectures and parameters. It can’t be known in advance how long this research will take or the results it will bring. How can you manage this uncertainty when you’re leading a project? <Link to="/articles/time-cost-and-outcomes-in-data-science-projects">Read more..</Link></p>

  </div>
);



